// see https://github.com/Semantic-Org/Semantic-UI-React/blob/master/docs/src/layouts/HomepageLayout.js

import React, { Component } from 'react';
import { createMedia } from '@artsy/fresnel';
import { Link } from 'react-router-dom';
import { Container, Icon, Menu, Segment, Sidebar, Visibility } from 'semantic-ui-react';
import styled from 'styled-components';
import Footer from './Footer';

const TOP_HEIGHT = '43px';

const HEADER_LINKS = [{ title: 'Address', to: '/address' }];

const Menus = () =>
  HEADER_LINKS.map(header =>
    header.anchor ? (
      <a href={header.to} rel="noreferrer" target="_blank">
        <Menu.Item className="pointer" as="a">
          {header.title}
        </Menu.Item>
      </a>
    ) : (
      <Link to={header.to} key={header.title}>
        <Menu.Item className="pointer" as="a">
          {header.title}
        </Menu.Item>
      </Link>
    )
  );

const { MediaContextProvider, Media } = createMedia({
  breakpoints: {
    mobile: 0,
    tablet: 768,
    computer: 1024,
  },
});

class DesktopContainer extends Component {
  state = {};

  hideFixedMenu = () => this.setState({ fixed: false });
  showFixedMenu = () => this.setState({ fixed: true });

  render() {
    const { children } = this.props;
    const { fixed } = this.state;

    return (
      <Media greaterThan="mobile">
        <Visibility
          style={{ height: TOP_HEIGHT }}
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment inverted textAlign="center" style={{ height: TOP_HEIGHT, padding: '0em' }} vertical>
            <Menu fixed={fixed ? 'top' : null} inverted={!fixed} pointing={!fixed} secondary={!fixed} size="large">
              <Container>
                <Menus />
                <Menu.Item position="right"></Menu.Item>
              </Container>
            </Menu>
          </Segment>
          {children}
        </Visibility>
      </Media>
    );
  }
}

class MobileContainer extends Component {
  state = {};

  handleSidebarHide = () => this.setState({ sidebarOpened: false });
  handleToggle = () => this.setState({ sidebarOpened: true });

  render() {
    const { children } = this.props;
    const { sidebarOpened } = this.state;

    return (
      <Media at="mobile" as={Sidebar.Pushable}>
        <Sidebar.Pushable>
          <Sidebar
            as={Menu}
            animation="overlay"
            inverted
            onHide={this.handleSidebarHide}
            vertical
            visible={sidebarOpened}
          >
            <Menus />
          </Sidebar>

          <Sidebar.Pusher dimmed={sidebarOpened}>
            <Segment inverted textAlign="center" style={{ height: TOP_HEIGHT, padding: '0em' }} vertical>
              <Menu inverted pointing secondary size="large">
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name="sidebar" />
                  <span>Menu</span>
                </Menu.Item>
                <Menu.Item position="right"></Menu.Item>
              </Menu>
            </Segment>

            {children}
          </Sidebar.Pusher>
        </Sidebar.Pushable>
      </Media>
    );
  }
}

const ResponsiveContainer = ({ children, query }) => (
  <MediaContextProvider>
    <MobileContainer query={query}>{children}</MobileContainer>
    <DesktopContainer query={query}>{children}</DesktopContainer>
  </MediaContextProvider>
);

const MainSegment = styled(Segment)`
  min-height: calc(100vh - ${TOP_HEIGHT});

  @media (max-width: 800px) {
    min-height: 150vh;
  }
`;

const DefaultLayout = ({ children, query }) => (
  <ResponsiveContainer query={query}>
    <MainSegment vertical>
      <Container>{children}</Container>
    </MainSegment>

    <Footer />
  </ResponsiveContainer>
);

export default DefaultLayout;
